<template>
<!-- Modal -->
<div class="modal fade" id="changePasswordModal" data-bs-backdrop="static" tabindex="-1" aria-labelledby="exampleModalLabel" aria-hidden="true">
  <div class="modal-dialog modal-dialog-centered">
    <div class="modal-content">
      <div class="modal-body">
        <div class="row modalHeader">
            <h5 class="modal-title" id="exampleModalLabel">ÎπÑÎ∞ÄÎ≤àÌò∏ Î≥ÄÍ≤Ω</h5>
        </div>
        <form action="" class="row g-3" style="text-align: left;">
          <div class="col-12" >
            <!-- nowPassword -->
            <div class="row p-0 mb-0 justify-content-between">
              <div class="col-8">
                <label for="inputAddress" class="form-label">ÌòÑÏû¨ ÎπÑÎ∞ÄÎ≤àÌò∏</label>
                <div class="input-group has-validation">
                  <input type="password" class="form-control" id="nowPassword"
                  v-model="state.nowPassword"
                  placeholder="ÌòÑÏû¨ ÎπÑÎ∞ÄÎ≤àÌò∏ ÏûÖÎ†•"
                  :disabled="state.isValidNowPassword"
                  required
                  autofocus>
                </div>
              </div>
              <div class="col-4 d-flex justify-content-end">
                <button class="btn btn-success text-black  btn-sm"
                  :disabled="!state.nowPassword || state.isValidNowPassword"
                  @click="onClickConfirmNowPassword"
                >ÎπÑÎ∞ÄÎ≤àÌò∏ ÌôïÏù∏
                </button>
              </div>
              <div
              :style="{ visibility: (!state.isValidNowPassword )? 'hidden' : 'visible' }"
              class="valid-feedback">
                ÎπÑÎ∞ÄÎ≤àÌò∏Í∞Ä ÌôïÏù∏ÎêòÏóàÏäµÎãàÎã§.
              </div>
            </div>
          </div>

          <!-- newPassword -->
          <div class="col-12">
            <label for="inputAddress2" class="form-label">Î≥ÄÍ≤ΩÌï† ÎπÑÎ∞ÄÎ≤àÌò∏</label>
            <input type="password" class="form-control" id="newPassword" v-model="state.newPassword"
              placeholder="Î≥ÄÍ≤ΩÌï† ÎπÑÎ∞ÄÎ≤àÌò∏ ÏûÖÎ†•" required>
            <div
            :style="{ visibility: (state.isValidNewPassword || !state.wasInputed.newPassword)? 'hidden' : 'visible' }"
            class="invalid-feedback">
              Ïà´Ïûê, ÏòÅÎ¨∏, ÌäπÏàòÎ¨∏ÏûêÎ•º Î™®Îëê Ìè¨Ìï®Ìï¥ 8 ~ 16 Í∏ÄÏûêÎ•º ÏûÖÎ†•Ìï¥Ï£ºÏÑ∏Ïöî.
            </div>
          </div>

          <!-- passwordConfirm -->
          <div class="col-12">
            <label for="inputAddress2" class="form-label">Î≥ÄÍ≤ΩÌï† ÎπÑÎ∞ÄÎ≤àÌò∏ ÌôïÏù∏</label>
            <input type="password" class="form-control" id="passwordConfirm" v-model="state.passwordConfirm"
              placeholder="Î≥ÄÍ≤ΩÌï† ÎπÑÎ∞ÄÎ≤àÌò∏ ÌôïÏù∏" required>
            <div
              v-if="!state.isValidPasswordConfirm && state.wasInputed.passwordConfirm"
              class="invalid-feedback">
              ÎπÑÎ∞ÄÎ≤àÌò∏ÏôÄ ÏùºÏπòÌïòÏßÄ ÏïäÏäµÎãàÎã§.
            </div>
            <div v-else-if="(state.passwordConfirm !== '') && state.isValidPasswordConfirm" class="valid-feedback">ÏùºÏπòÌï©ÎãàÎã§.</div>
            <div v-else style="height: 1.3rem;"></div>
          </div>
        </form>
        <div class="row modalFooter">
          <div class="col" style="margin: 10px;">
            <button type="button" class="btn btn-secondary" data-bs-dismiss="modal" @click="onClickCancle">Ï∑®ÏÜå</button>
            <button type="button" class="btn btn-primary" @click="onClickChange">ÎπÑÎ∞ÄÎ≤àÌò∏ Î≥ÄÍ≤Ω</button>
          </div>
        </div>
      </div>
    </div>
  </div>
</div>
</template>

<script>
/* eslint-disable */
import { updatePassword, confirmpassword } from '@/api/user';
import { reactive, computed } from 'vue';
import { useRouter } from 'vue-router';
import notifications from '@/composables/notifications'

export default {
  name: '',
  setup(){
    const state = reactive({
      nowPassword: '',
      newPassword: '',
      passwordConfirm: '',
      wasInputed: {
        nowPassword: false,
        newPassword: false,
        passwordConfirm: false,
      },
      isValidNowPassword: false,

      isValidNewPassword: computed(() => {
        if (state.newPassword !== '') {
          state.wasInputed.newPassword = true
        }
        if (state.newPassword && validatePassword(state.newPassword)) {
          return true;
        }
        return false;
      }),
      isValidPasswordConfirm: computed(() => {
        if (state.passwordConfirm !== '') {
          state.wasInputed.passwordConfirm = true
        }
        if (state.passwordConfirm && (state.newPassword === state.passwordConfirm)) {
          return true;
        }
        return false;
      }),
    })
    const { notifyDanger, notifySuccess } = notifications();

    const validatePassword = function (password) {
      const numberChar = /[0-9]/;
      const specialChar = /[`~!@#$%^&*\\\'\";:\/?]/;
      const alphabetChar = /[a-zA-Z]/;
      if (password.length >= 8 && password.length <= 16) {
        if (numberChar.test(password) && specialChar.test(password) && alphabetChar.test(password)) {
          return true;
        }
      } return false;
    };

    const router = useRouter();

// ÎπÑÎ∞ÄÎ≤àÌò∏ ÌôïÏù∏
    const onClickConfirmNowPassword = function(e) {
      e.preventDefault()
      confirmpassword(
        { password: state.nowPassword },
        (res) => {
          switch (res.data.code) {
            case 200:
              state.isValidNowPassword = true;
              notifySuccess('ÎπÑÎ∞ÄÎ≤àÌò∏ ÌôïÏù∏ ÏôÑÎ£å!üòÄ')
              break;
            case 400:
              state.isValidNowPassword = false;
              notifyDanger('ÎπÑÎ∞ÄÎ≤àÌò∏ ÌôïÏù∏ Ïã§Ìå®üò∞')
              break;
          }
        },
        (err) => {
          console.log(err)
          notifyDanger('ÎπÑÎ∞ÄÎ≤àÌò∏ Î∂àÏùºÏπòüò∞')
          state.nowPassword = ''
          state.wasInputed.nowPassword = false
          state.isValidNowPassword = false
        },
      )
    }

    const onClickChange = function(e){
      e.preventDefault();
      if (state.nowPassword === '' || state.newPassword === '' || state.passwordConfirm === '' ) {
        state.wasInputed.nowPassword = true;
        state.wasInputed.newPassword = true;
        state.wasInputed.passwordConfirm = true;
        return;
      }
      if (!state.isValidNewPassword || !state.isValidPasswordConfirm || !state.isValidNowPassword){
        return;
      }


      // ÎπÑÎ∞ÄÎ≤àÌò∏ ÏàòÏ†ï
      updatePassword(
        { password: state.passwordConfirm },
        (res) => {
          switch (res.data.code) {
            case 200:
              router.go({ name: 'MyPage' })
              notifySuccess('ÎπÑÎ∞ÄÎ≤àÌò∏ ÏàòÏ†ï ÏÑ±Í≥µ!ü§ó')
              break;
            case 404:
              notifyDanger('ÎπÑÎ∞ÄÎ≤àÌò∏ ÏàòÏ†ï Ïã§Ìå®üò∞')
              break;
            case 400:
              notifyDanger('ÌöåÏõê Ïù∏Ï¶ù Ïã§Ìå®üò∞');
              break;
          }

        },
        (err) => {
          console.log(err)
          notifyDanger('ÏÑúÎ≤ÑÏóê Î¨∏Ï†úÍ∞Ä Î∞úÏÉùÌñàÏäµÎãàÎã§.üò∞')
        }
      )
    }

    const onClickCancle = function(e){
      e.preventDefault()
      state.nowPassword = ''
      state.newPassword = ''
      state.passwordConfirm = ''
      state.isValidNowPassword = false
      state.wasInputed.nowPassword = false
      state.wasInputed.newPassword = false
      state.wasInputed.passwordConfirm = false
    }

    return {
      state,
      onClickConfirmNowPassword,
      onClickChange,
      onClickCancle,
    };
  }
}
</script>

<style scoped>
input::placeholder {
  font-size: 12px;
  padding: auto;
  vertical-align: middle;

}
input[type="password"]{
  background-color: #F4F5F4;
  vertical-align: middle;
}
.form-label{
  margin-bottom: 3px;
}
.modal-title {
  font-size: 24px;
  text-align: center;
  margin-bottom: 30px;
}

.row{
  margin-bottom: 10px;
  padding: 10px 15px;
}

.modalHeader, .modalFooter{
  margin: 20px;
}

.invalid-feedback {
  display: block;
  font-size: 0.75rem;
  margin-top: 0;
  margin-bottom: 0.5rem;
}
.valid-feedback {
  display: block;
  font-size: 0.75rem;
  margin-top: 0;
  margin-bottom: 0.5rem;
  color: green;
}
.btn-success{
  margin-top: 1.7rem;
  height: 2.4rem;
}
.modalFooter{
  padding: 0px;
  margin-top: 0px;

}
.modalFooter button {
  margin-inline: 0.5rem
}
</style>
